body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.main-pokemon {
	display: flex;
	flex-direction: column;

	margin-top: 100px;
	margin-bottom: 50px;
}

.header-main-pokemon {
	margin-top: 20px;

	display: flex;
	align-items: center;

	position: relative;
}

.number-pokemon {
	position: absolute;
	top: -70px;
	left: -80px;

	font-size: 200px;
	font-weight: 700;
	color: var(--color-primary-hover);
}

.container-img-pokemon {
	order: 2;
	flex: 1 1;
	padding: 25px 25px 25px 0;
	height: 400px;
}

.container-img-pokemon img {
	height: 100%;
	width: 100%;
}

.container-info-pokemon {
	display: flex;
	flex-direction: column;

	order: 1;
	flex: 1 1;
}

.container-info-pokemon h1 {
	text-align: right;
	font-size: 60px;
}

.info-pokemon {
	display: flex;
	justify-content: space-between;

	margin-top: 20px;
}

.group-info{
	display: flex;
	flex-direction: column;
	grid-gap: 10px;
	gap: 10px;
}

.group-info p {
	font-weight: 700;
	font-size: 20px;
}

/* Stats */
.container-stats {
	display: flex;
	grid-gap: 50px;
	gap: 50px;
	align-items: center;
}

.stats {
	display: flex;
	flex-direction: column;
	grid-gap: 20px;
	gap: 20px;
	flex: 1 1;
}

.stat-group {
	display: flex;
	align-items: center;
	grid-gap: 20px;
	gap: 20px;
}

.stat-group span {
	flex: 20% 1;
	font-weight: 500;
	font-size: 18px;
}

.progress-bar {
	width: 100%;
	height: 30px;
	background-color: var(--color-primary);
	border-radius: 10px;
}

.progress{
	height: 30px;
	background-color: red;
	border-radius: 10px;
}


.btns{
	display: flex;
	justify-content: space-around;
	position: relative;
	top: 40px;
}

.btns button{
	background-color: aquamarine;
	font-size: xx-large;
	cursor: pointer;
	border-style: 20px solid green;
	border-radius: 10px;
	padding: 10px;
}
.container-mess{
    display: block;
    background-color: rgb(202, 202, 202);
    min-height: 100vh;
    min-width: 10vh;
}



.message{
display: flex;
align-items: center;
margin: 0px 20px 0px 250px;
}

.message h1{
    color:rgb(110, 110, 110);
    font-size: 500px;
}

.message img{
    height: 400px;
}


.infor{
    font-family: 'Press Start 2P', cursive;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 15px;
    gap: 15px;
}

.infor button{
    border: none;
    outline: none;
    border-radius: 10px;
    padding: 10px 30px;
    color: #fff;
    font-weight: 700;
    border-style:outset;
    font-family: 'Press Start 2P', cursive;
    cursor: pointer;
    background-color: brown;
}

.infor button:hover{
    color:black;
    background-color: rgb(255, 255, 1);
}
.infor button:active{
    color:rgb(255, 255, 255);
    background-color:green ;
}

