/* Container Filters */
.container-filters {
	display: flex;
	justify-content: center;
	position: fixed;
	top: 0;
	left: -300px;
	width: 250px;
	color: #555;
	background-color: #dedddd;
	height: 100%;
	padding-top: 48px;
	transition: all 0.3s linear;
}

.container-filters.active {
	left: 0;
}

.filter-by-type {
	display: flex;
	flex-direction: column;
	grid-gap: 20px;
	gap: 20px;
}

.filter-by-type span {
	font-weight: 700;
	font-size: 22px;
}

.filter-by-type select{
	border-radius: 10px;
	border-style: double;
	padding: 10px;
	display: inline;
	margin: 0%;
	background-color: rgb(189, 193, 193);
	cursor: pointer;
}

.filter-by-type option{
	font-style: oblique;
	font-family:fantasy;
	font-size: 15px;
}
